.blog-page {
  flex-wrap: wrap;
  padding: 150px 0 100px;
  width: 100%;
  max-width: 1237px;
  margin: 0 auto;
}
.blog-page .main {
  margin: 0 auto;
  padding-bottom: 0;
  display: flex;
  justify-content: space-between;
  height: unset;
  padding-top: 0;
}
.blog-page main {
  max-width: 1035px;
  width: 100%;
  padding: 0 20px;
}
.blog-page main h1 {
  font-weight: bold;
  font-size: 24px;
  line-height: 35px;
  color: #000624;
  margin-bottom: 1px;
  display: flex;
  align-items: center;
}
.blog-page main h1 button {
  height: fit-content;
  margin-top: 5px;
  margin-left: 8px;
}
@media (min-width: 880px) {
  .blog-page main h1 button {
    display: none;
  }
}
.blog-page main > p {
  font-size: 16px;
  line-height: 24px;
  color: #5f5f68;
}
.blog-page .card {
  background: #ffffff;
  box-shadow: 10px 20px 81px rgba(111, 118, 138, 0.08);
  border-radius: 8px;
}
.blog-page .dropdown-container {
  position: relative;
}
.blog-page .dropdown-container .dropdown {
  position: absolute;
  background: #ffffff;
  box-shadow: 0px 2px 4px rgba(12, 26, 75, 0.04), 0px 4px 20px -2px rgba(50, 50, 71, 0.08);
  border-radius: 10px;
  z-index: 1;
  width: 217px;
  margin-top: 4px;
  height: 0;
  padding: 0;
  overflow: hidden;
}
.blog-page .dropdown-container .dropdown.show {
  height: fit-content;
  padding: 24px;
}
.blog-page .dropdown-container .dropdown li {
  margin: 0 0 32px;
  font-size: 14px;
  line-height: 22px;
  color: #5f5f68;
}
.blog-page .dropdown-container .dropdown li:last-of-type {
  margin-bottom: 0;
}
.blog-page footer {
  background: transparent;
}